<template>
    <div>
      <div class="city">
        <router-link to="/city">
        <div class="triangle_border_down">
             <span></span>
        </div>
        </router-link>
      </div>
        <ul class="home-list" >
            <li  class="home" v-for="home in homeList" :key="home.id" @click="goDetail(home.id)">
                <div class="home-img" ><img :src="home.img" /></div>
                <div class="home-info">
                    <p>{{home.title}}</p>
                    <p>{{home.content}}</p>
                    <span class="sale">{{home.sale}}</span>
                    <p>￥:<span class="price">{{home.price}} </span></p>
                </div>
            </li> 
          
        </ul>         
    </div>
</template>

<script>
import Axios from "axios";

export default {
  data() {
    return {
      homeList: [], 
    }
  },
  methods:{
    
    loadData(){
      let url2 = "./static/data/goods.json";

      Axios.get(url2)
        .then(res => {
            // console.log(res);
          
          this.homeList = res.data.data.homes;
        //   console.log(this.homeList)
        })
        .catch(() => {
         alert("数据接收失败!");
        });
    },
    goDetail(homeId){
      this.$router.push('/home/homeDetail' );
    },
  },
  mounted(){
    this.loadData()
  } 
};
</script>

<style scoped>
.city{width:0.15rem;height:0.15rem;position: absolute;top: 0.5rem;right: 0.5rem;}
.triangle_border_down{
  position: fixed;
}
.triangle_border_down span{
    display:block;
    width:0;
    height:0;
    border-width:7px 7px 0;
    border-style:solid;
    border-color:#fff transparent transparent;/*黄 透明 透明 */
    position:absolute;
    top:0px;
    left:0px;
}

.home-list {
  margin: 1rem 0;
}
.home {
  display: flex;
  padding: 0.2rem;
  border-bottom: 1px solid #ccc;
}
.home-img {
  flex: 1 1 0;
  margin-top: -0.2rem;
  margin-right: 0.2rem;
}
.sale{float: right;color: #777;margin-right:0.3rem; }
.home-info {
  flex: 2 1 0;
}
.home-info p:first-child {
  font-weight: bold;
  color: 000;
}
.loading {
  text-align: center;
  margin-bottom: 1rem;
}
.tip{
  text-align: center;
}
.price{color:#FF80C2;font-size: 0.3rem;font-weight: bold;}

</style>


