<template>
<div>
    <div>
        <h5 class="ding">定位城市</h5>
        <div class="city-ding"></div>
        <h5 class="hot">热门城市</h5>
        <ul class="hot-city">
            <li>
                <a>上海</a>
                <a>北京</a>
                <a>广州</a>
            </li>
            <li>
                <a>深圳</a>
                <a>武汉</a>
                <a>天津</a>
            </li>
            <li>
                <a>西安</a>
                <a>重庆</a>
                <a>杭州</a>
            </li>
        </ul>
        <ul class="city" @click="back">
            <li v-for="city in cityList" :key="city">
                <p id="bian">{{city.n}}</p>
                <ul class="shi">
                    <li >
                        <p>{{city.c[0]}}</p>
                        <p>{{city.c[1]}}</p>
                        <p>{{city.c[2]}}</p>
                        <p>{{city.c[3]}}</p>
                        <p>{{city.c[4]}}</p>
                        <p>{{city.c[5]}}</p>
                        <p>{{city.c[6]}}</p>
                        <p>{{city.c[7]}}</p>
                        <p>{{city.c[8]}}</p>
                        <p>{{city.c[9]}}</p>
                        <p>{{city.c[10]}}</p>
                        <p>{{city.c[11]}}</p>
                        <p>{{city.c[12]}}</p>
                        <p>{{city.c[13]}}</p>
                        <p>{{city.c[14]}}</p>
                        <p>{{city.c[15]}}</p>
                        <p>{{city.c[16]}}</p>
                        <p>{{city.c[17]}}</p>
                        <p>{{city.c[18]}}</p>
                        <p>{{city.c[19]}}</p>
                        <p>{{city.c[20]}}</p>
                        <p>{{city.c[21]}}</p>
                        <p>{{city.c[22]}}</p>
                        <p>{{city.c[23]}}</p>
                        <p>{{city.c[24]}}</p>
                        <p>{{city.c[25]}}</p>
                        <p>{{city.c[26]}}</p>
                        <p>{{city.c[27]}}</p>
                        <p>{{city.c[28]}}</p>
                        <p>{{city.c[29]}}</p>
                        <p>{{city.c[30]}}</p>
                        <p>{{city.c[31]}}</p>
                        <p>{{city.c[32]}}</p>
                        <p>{{city.c[33]}}</p>
                        <p>{{city.c[34]}}</p>
                        <p>{{city.c[35]}}</p>
                        <p>{{city.c[36]}}</p>
                        <p>{{city.c[37]}}</p>
                        <p>{{city.c[39]}}</p>
                        <p>{{city.c[40]}}</p>
                        <p>{{city.c[41]}}</p>
                        
                    </li>
                </ul>
            </li>
        </ul>
    </div>
   
</div>  
</template>

<script>
import Axios from "axios";
export default {
    data(){
        return{
            cityList:[],
            shiList:[],
        }
    },
    methods:{
    loadData(){
      let url2 = "./static/data/city.json";
      Axios.get(url2)
        .then(res => {
            // console.log(res);
          
          this.cityList = res.data.city;
         
        })
        .catch(() => {
         alert("数据接收失败!");
        });
    },
    back(){
      this.$router.push('/')
     }
  },
  mounted(){
    this.loadData()
  } 
}
</script>
<style scoped>
h5.ding{width:100%;height: 0.5rem;background: #EBEBEB;
text-indent:0.5rem;line-height:0.5rem; }
.city-ding{width: 100%;height: 0.5rem;background:#F5F5F5; }
h5.hot{width:100%;height: 0.5rem;background: #EBEBEB;
text-indent:0.5rem;line-height:0.5rem;}
.shi{margin: 10px;}
#bian{width:100%;background: #EBEBEB;}
.hot-city{width: 100%;height: 3rem;background: #F5F5F5;overflow: hidden;}
.hot-city li{height: 30%;}
.hot-city li:nth-child(1){margin-top: 0.3rem;}
.hot-city li a{display: block;color: #000;background: #fff;border: 1px solid #ccc;margin-left: 0.7rem;float: left;font-size:0.25rem;padding-left: 0.2rem;padding-right: 0.2rem;padding-top:0.1rem;}
p{font-size: 0.3rem;text-indent: 0.3rem;}
</style>




