import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/home/Home'
import HomeList from '@/components/home/HomeList'
import HomeDetail from '@/components/home/HomeDetail'
import Cart from '@/components/cart/Cart'
import CartList from '@/components/cart/CartList'
import CartDetail from '@/components/cart/CartDetail'
import My from '@/components/my/My'
import MyList from '@/components/my/MyList'
import MyDetail from '@/components/my/MyDetail'
import MyAddress from '@/components/my/MyAddress'
import City from '@/components/city/City'
Vue.use(Router)

export default new Router({
  // mode: 'history', //去除URL中的"#"
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/home/homelist'
    },
    {
      path:'/home',
      name:'Home',
      component:Home,
      children:[{
        path: 'HomeList',
        name: 'HomeList',
        component: HomeList
      },
      {
        path: 'homeDetail/:homeId',
        name: 'HomeDetail',
        component: HomeDetail
      }
    ]
    },
    {
      path:'/cart',
      name:'Cart',
      component:Cart,
      redirect: '/cart/cartList',
      children:[
        {
        path:'cartList',
        name:'CartList',
        component:CartList,
       },
       {
        path:'cartDetail/:index',
        name:'CartDetail',
        component:CartDetail,
       }
      ]
    },
    {
      path:'/my',
      name:'My',
      component:My,
      redirect: '/my/myList',
      children:[
        {
          path: 'myList',
          name: 'MyList',
          component: MyList
        },{
          path: 'myDetail',
          name: 'MyDetail',
          component: MyDetail,
        },
        {
            path: 'myAddress',
            name: 'MyAddress',
            component: MyAddress,
        }
      ]
    },
    {
      path: '/city',
            name: 'City',
            component: City,
    }
  ]
})



