<template>

    <!-- <div>
         <div class="back">
            <span class="back" @click="goHome">
                <img src="../../../static/imgs/arrow.png"/>
            </span>
         </div>  -->
    
            <v-touch class="detail" :style="styleObj" @swipeleft="swipeLeft" @swiperight="swipeRight"
            @swipedown="swipeDown" @tap="tap"
          >
           </v-touch>
    <!-- </div> -->
</template>
<script>

export default {
    data(){
        return {              
            nowIndex: 0, //获取用户当前选中的图片的下标
        }
    },
      mounted(){    
        this.nowIndex = this.$route.params.index;
        console.log('接收的参数ID= '+this.nowIndex); //接收route传参     
    },
    computed: {
        styleObj(){
           // console.log('nowindex='+this.nowIndex);
            return{
                background: `#fff url(${this.$store.state.photoList[this.nowIndex].src}) no-repeat center / contain`   
            }  
        }
    },
     methods: {  
        swipeLeft(){//左右滑动原理: 类似轮播图
            this.nowIndex++;
            if(this.nowIndex == this.$store.state.photoList.length ){
                this.nowIndex = 0
            }
            //console.log("Left! 左左!");
        },
        swipeRight(){
            this.nowIndex--;
            if(this.nowIndex < 0 ){
                this.nowIndex = this.$store.state.photoList.length - 1;
            }
            console.log("right!!!");
        },
        swipeDown(){
            console.log("Down!!!");
        },
        tap(){
            this.$router.go(-1);
           // console.log('tappppp');
        }
    }
   
}
</script>
<style scoped>
div.detail{margin:1rem 0; position:absolute; 
top:0; right:0; bottom:0; left:0;
background: #ccc;}
.back{float: left;position:absolute;
z-index: 99;top:0.15rem;left: 0.2rem;width: 0.5rem;height: 0.5rem;}
.back span{color: #fff;}
.back span img{width:100%;}
</style>




