<template>
    <div  class="login-page">
      <div class="login">
        <el-form>
          <el-form-item>
            <el-input v-model="username" placeholder="请输入帐号"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input type="password" v-model="password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click="login">登录</el-button>
            <el-button>注册</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
</template>
<script>
import Axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      username: '例如:翠花儿',
      password: '123456',
    }
  },
  methods: {
       login () {
      this.$store.commit('login', this.username);
       this.$router.push('/my/mydetail')
    },
    // toPrePage () {
    //   this.$router.go(-1);
    // }
  }
}
</script>
<style scoped>
    /* button{border:none;outline: none;background: pink;width: 1.5rem;height: 0.7rem;position: absolute;bottom:50%;left: 50%;margin-left: -0.7rem;}
    button a{color: #fff;font-size: 0.3rem;} */

    .login{width:5.2rem;height:50%;margin-left:0.4rem;margin-top: 4rem; }
    button{margin-left: 1.2rem;} 
    input{border: none;outline: none;}

</style>



