<template>
    <div class="edit">
      <header>
        <h3>我的收货地址</h3>
      </header>
      <main>
        <ul class="all-address" >
          <li class="address" v-for="address in allAddress" :key="address.id">
            <p>{{ address.name }}</p>
            <p>{{ address.phone }}</p>
            <p>{{ address.city + address.detailAdd }}</p>
            <el-button type="text" @click="toMyAddress">确定</el-button>
          </li>
        </ul>
      </main>
      <footer>
        <!-- <el-button type="danger" @click="toAdressInputForm">
          新增收货地址
        </el-button> -->
      </footer>
    </div>
</template>
<script>
import Axios from "axios";
export default {
   data() {
    return {
        allAddress:[],
    }
  }, 
  methods: {
   loadData(){
      let url3 = "./static/data/allAddress.json";

      Axios.get(url3)
        .then(res => {
            console.log(res);
          
          this.allAddress = res.data.data.address;
          console.log(this.allAddress)
        })
        .catch(() => {
         alert("数据接收失败!");
        });
    },
     toMyAddress(){
     this.$router.push('/my/myDetail')
    },
  },
  mounted () {
      this.loadData()
  } 
  

}
</script>
<style scoped>
.edit{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1001;
  background-color: #f5f5f5;
 }
header{
    position: relative;
    height: 50px;
    border-bottom: 1px solid #eee;
    line-height: 50px;
    background-color: #fff;}
main{
    margin-top: 10px;
    text-align: left;
    line-height: 23px;}
.all-address{
      background-color: #fff;}
.address{
        position: relative;
        padding: 10px;
        border-top: 1px solid #eee;}
.el-button{
          position: absolute;
          right: 10px;
          top: 50%;
          transform: translateY(-50%);
        }
footer{
    position: fixed;
    width: 100%;
    bottom: 10px;
    left: 0;}
.el-button--danger{
      width: 90%;
      background-color: #ccc;
      
}
</style>




