<template>
   <div class="detail">
        <div class="detail-content">
            <div class="detail-img">
                <img :src="homeDetail[0].img" />
            </div>
            <div class="detail-info">
                <span class="label">{{homeDetail[0].xindong}}</span>
                <h2 v-text="homeDetail[0].price"></h2>
                    <button type="button" @click="minus()" id="b">-</button>
                    <button type="button" id="b">{{ result }}</button>
                    <button type="button" @click="plus()" id="b" >+</button>
             <!-- <button id="qiang">{{homeDetail[0].qiang}}</button> -->
            </div>
            <h4>{{homeDetail[0].need}}</h4>
            <h5>{{homeDetail[0].day}}</h5>
            <p>{{homeDetail[0].xiang}}</p>
            <h5>{{homeDetail[0].time}}</h5>
            <p>{{homeDetail[0].jian}}</p>
            <h5>{{homeDetail[0].regular}}</h5>
            <p>{{homeDetail[0].end}}</p>
        </div>
     <div class="back">
        <span class="back" @click="goHome">
            <img src="../../../static/imgs/arrow.png"/>
        </span>
      </div> 
    </div>
</template>
<script>
import Axios from 'axios'

export default {
    data(){
        return {
            homeDetail: [],
            result: 0,
           homes:[],
        }
    },
    methods: {
        goHome(){
      this.$router.push('/')
    }, 


minus() {
this.result--;
if(this.result<=0){
  this.result=0;
}
else{this.$emit('input', {res: this.result, other: '--'})
}
},
plus() {
this.result++;
this.$emit('input', {res: this.result, other: '++'});

},



  
    },
    mounted() {
          let url2 = './static/data/good'+this.$route.params.homeId+'.json'

        Axios.get(url2).then(res => {
            console.log(res);
            this.homeDetail = res.data.data.homes;
            console.log(this.homeDetail);
        }).catch(() => {
          alert("数据接收失败!");
        });
    
    
    },
  
}
</script>
<style scoped>
.detail{ margin: 1rem 0; padding:0.2rem;position: relative;}
.detail-content{width: 100%; margin-bottom: 0.2rem;}
.detail-img{ width: 100%;height: 3.5rem; text-align: center;border-bottom: 1px solid #DDDDDD;}
.detail-info{width:100%;height: 1rem;border-bottom: 1px solid #DDDDDD;margin-top: 0.3rem;}
.detail-info .label{position: absolute;top:4.2rem;left: 0.5rem;;}
.detail-info h2{float:left;width: 30%;color: #6CC1FA;padding-left:1.2rem;margin-top:-0.1rem;}
h4{padding-left:0.3rem;border-bottom: 1px solid #DDDDDD;}
h5{padding-left:0.3rem;color:#6CC1FA;}
#qiang{background:pink;border: none;color: #fff;width: 2rem;height: 0.7rem;font-size: 0.3rem;outline: none;position: fixed;top:9.5rem;right: 0.2rem; }
.back{float: left;position:absolute;z-index: 99;top:-0.4rem;left: 0.2rem;width: 0.5rem;height: 0.5rem;}
.back span{color: #fff;}
.back span img{width:100%;}
#b{border: 0;outline :0;padding-top: 0.07rem;padding-right: 0.2rem;
width :0.5rem;background:pink;margin-bottom: 1.2rem;color:#fff;
float:right;height: 0.5rem;font-size: 0.3rem; 
}
.clear_fix{clear: both;}
</style>



