import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    username: '', 
    isLogin: false,
    photoList : [],
    
    
}

const mutations = {
    setPhotoList(state, status){ 
        state.photoList = status 
    },


    login(state, username){
        state.username = username;
        state.isLogin = true;
      },
      logout(state){
        state.isLogin = false;
      },
}
const getters = {

}

const actions = {actionPhotoList({commit}, status){ 
                commit('setPhotoList', status);
}
}

export default new Vuex.Store({
    state,
    mutations,
    getters,
    actions,
});





