<template>
    <div>
         <div class="back">
            <span class="back" @click="goHome">
                <img src="../../../static/imgs/arrow.png"/>
            </span>
         </div> 
      <div class="profile">
      <div class="user" >
          <header class="profile-header">
            <h3>当前用户：{{ username }}</h3>
          </header>
          <main class="profile-body">
            <div class="address">
              <el-button @click.stop="toMyAddressPage">我的地址<i class="el-icon-arrow-right"></i></el-button>
            </div>
          </main>
          <footer class="profile-footer">
            <div>
              <el-button type="danger" @click="logout">退出登录</el-button>
            </div>
          </footer>
      </div>
      <transition enter-active-class="slideInRight">
        <router-view></router-view>
      </transition>
  </div>
    </div>
</template>
<script>
import Axios from 'axios'
export default {
  name: 'profile',
  data () {
    return {
    }
  },
  computed: {
    username () {
      return this.$store.state.username;
    },
    isLogin () {
      return this.$store.state.isLogin;
    }
  },
    methods: {
        goHome(){
      this.$router.push('/my/mylist')
    },
    
    
    toLogin () {
      this.$router.push({
        path: '/my/mylist',
      })
    },
    toMyAddressPage () {
      this.$router.push({
        path: '/my/myAddress'
      })
    },
    logout () {
      this.$store.commit('logout');
      this.$router.push({
        path: '/my/mylist'
      })
    }
  },
 }

</script>
<style scoped>
.back{float: left;position:absolute;z-index: 99;top:0.15rem;left: 0.2rem;width: 0.5rem;height: 0.5rem;}
.back span{color: #fff;}
.back span img{width:100%;}
.profile{
  height: 100%;
  background-color: #000;
  position: relative;
  margin-top:5rem;
}
.login{
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);}
.login .el-input{
    width: 80%;
    margin-bottom: 10px;
    border: 1px solid blue;
    font-size: 0.2rem;
  }
.profile-header{
    height: 1rem;
    line-height: 1rem;
  }
h3{font-size: 0.3rem;}
.user{
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  }
.profile-footer .el-button{
    width: 100%;
    font-size:0.3rem;
    margin-top: 0.4rem;
  }
.address .el-button{width: 100%;
                    height:0.7rem;
                    float:left;
                    padding-left: 4rem;}
</style>




