<template>
    <div class="cart">
    <common-header></common-header>
        <swipe class="my-swipe">
            <swipe-item class="slide1"></swipe-item>
            <swipe-item class="slide2"></swipe-item>
            <swipe-item class="slide3"></swipe-item>
            <swipe-item class="slide4"></swipe-item>
            <swipe-item class="slide5"></swipe-item>
        </swipe>
     <div class="list clearfix">
        <ul>
            <li v-for="(photo, index) in photoList" :key="index">
                <router-link :to="'/cart/cartDetail/'+index">
                    <img :src="photo.src" />
                </router-link>
                
            </li>
        </ul>
    </div>
    <router-view></router-view>
    <common-footer></common-footer>
</div>
</template>
<script>
import CommonHeader from '../common/Header';
import CommonFooter from '../common/Footer';
import "vue-swipe/dist/vue-swipe.css";
import { Swipe, SwipeItem } from "vue-swipe";
import Axios from 'axios'
export default {
    components: {
        CommonHeader,
        CommonFooter,
        swipe: Swipe,
        "swipe-item": SwipeItem
    },


    data(){
        return {
            photoList: []
        }
    },
    mounted(){
        let url = './static/data/photodata.json'

        Axios.get(url).then(res => {
            // console.log(res);
            this.photoList = res.data.photoData

            //将数据存入vuex
            this.$store.dispatch('actionPhotoList', this.photoList);
           console.log(this.photoList);
        }).catch()
    }
}
</script>
<style scoped>
@import url('../../assets/css/common.css');
.cart{
    margin: 1rem 0;
}
.my-swipe {
  height: 180px;
  color: #fff;
  font-size: 30px;
  text-align: center;
 
}

.slide1 {
  background: url('/static/imgs/food1.jpg') center center;
  color: #fff;
}

.slide2 {
  background: url('/static/imgs/food2.jpg') center center;
  color: #fff;
}

.slide3 {
  background: url('/static/imgs/food3.jpg') center center;
  color: #fff;
}
.slide4 {
  background: url('/static/imgs/food4.jpg') center center;
  color: #fff;
}
.slide5 {
  background: url('/static/imgs/food5.jpg') center center;
  color: #fff;
}
.cart ul{width:100%;height: 1rem;}

.list{ margin: auto;}
.list li{width: 49%; float:left;}
.list li img{width: 100%;}

</style>





